* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  list-style: decimal;
}

ul > li:first-child > a:hover {
  color: teal;
  -webkit-text-decoration-color: #9acd32;
  text-decoration-color: #9acd32;
}

ul > :nth-child(2) > a:hover {
  color: #f57812;
  -webkit-text-decoration-color: #ffa601;
  text-decoration-color: #ffa601;
}

ul > :last-child > a:hover {
  color: #d01010;
  -webkit-text-decoration-color: red;
  text-decoration-color: red;
}

.input {
  width: 200px;
  border-radius: 50px;
  flex-direction: column;
  margin: auto auto 20px;
  display: flex;
  position: relative;
}

.input > input {
  color: #5d067b;
  text-align: center;
  border-radius: 5px;
  margin-bottom: 15px;
  position: relative;
}

.input > button {
  border-radius: 5px;
}

.btn-off {
  border-color: none;
  color: none;
}

.active-Btn {
  border-color: #57fb85;
  box-shadow: 0 13px 27px -5px rgba(16, 105, 16, .118), 0 8px 16px -8px rgba(0, 0, 0, .3);
}

.active-Btn:hover {
  cursor: pointer;
  color: #4c4285;
  background-color: #d4f8e8;
}

.timer {
  justify-content: center;
  font-size: 36px;
  display: flex;
}

.field {
  text-align: center;
  text-shadow: 1px 0 1px #ccc, 0 1px 1px #eee, 2px 1px 1px #ccc, 1px 2px 1px #eee, 3px 2px 1px #ccc, 2px 3px 1px #eee, 4px 3px 1px #ccc, 3px 4px 1px #eee, 5px 4px 1px #ccc, 4px 5px 1px #eee, 6px 5px 1px #ccc, 5px 6px 1px #eee, 7px 6px 1px #ccc, -14px -13px 2px rgba(206, 89, 55, 0);
  flex-direction: column;
  margin-left: 10px;
  margin-right: 10px;
  padding-bottom: 15px;
  display: flex;
  position: relative;
}

.value {
  color: #8906fb;
}

.label {
  color: #480286;
}

.field:not(:first-child):before {
  content: ":";
  position: absolute;
  left: -15px;
}

